body{
    margin: 0%;
    padding: 0%;
    /* font-family: 'Inter', sans-serif; */
    font-family: 'Roboto', sans-serif;
    background-color: #000;
    /* font-family: 'Inter', sans-serif;
    font-family: 'Roboto', sans-serif; */
}
bold{
    font-weight: 900;
}
.hero{
    background: url("./assets/herobg.png");
    background-size: cover;
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    color: #fff;
}
.hero >  h1{
    margin: 0%;
    margin-top: 10px;
    font-weight: bolder;
    font-size: 6vw;
}
.hero > p{
    max-width: 50%;
}
.buy{
    outline: none;
    border: none;
    border-radius: 8px;
    width: 200px;
    box-sizing: border-box;
    padding: 15px;
    font-size: large;
    background-color: #22F0DC;
    color: #000;
}
.why{
    width: 100%;
    height: fit-content;
    color: #fff;
    box-sizing: border-box;
    padding: 25px 0px;
}
.why > h1{
    margin: 0%;
    font-weight: 900;
    font-size: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
}
h1 > p{
    margin: 0%;
}
.highlight{
    color: #22F0DC;
    font-weight: 700;
    font-family: 'Oswald', sans-serif;
}
.container{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
    box-sizing: border-box;
    padding: 0px 20px;
    margin-top: 40px;
}
.img-cont{
    display: flex;
    align-items: center;
    justify-content: center;
}
.img-cont > img{
    height: 300px;
    aspect-ratio: initial;
    border-radius: 12px;
}
.text-cont{
    max-width: 45%;
}
.works{
    background-color: #512321;
    color: #fff;

}
.box{
    width: 300px;
    height:100%;
    background-color: aqua;
    transition: 1s;
    position: fixed;
    /* left: -100px; */
    top: 0px;
    right: -400px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    box-sizing: border-box;
    padding: 10px;
    gap: 20px;
}
.active{
    transform: translateX(-400px);
    width: 60%;
}
.logo{
    background: url(./assets/logo.jpg);
    background-size: contain;
    border-radius: 50%;
    width: 60px;
    height: 60px;
}
nav{
    background-color: #000;
    height: 80px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: white;
}
.link{
    text-decoration: none;
    color: white;
}
.toggleBtn{
    display: none;
}

.closeSlide{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
}

.hero > span{
    display: none;
}
